<template>
  <page
    @on-ready="onReady"
    ref="page"
    class="transmit-page common-page"
    :headerHeightVW="headerHeightVW"
    scrollbar
  >
    <van-nav-bar slot="header" id="header" title="转发详情" left-arrow @click-left="onClickLeft">
    </van-nav-bar>

    <detail v-if="detail.id" :data="detail"></detail>

    <div class="blank-empty"></div>

    <template slot="blank">
      <div class="footer-action" v-if="detail.state == 'to_confirmed' && detail.source=='receive'">
        <van-button class="action-item-main" color="#FF6166" @click="handleReject">拒绝</van-button>
        <van-button class="action-item-main" color="#108EE9" @click="handleAgree">同意</van-button>
      </div>
    </template>
  </page>
</template>

<script>
import Page from '../../components/Page'
import navigate from '../../utils/navigate.js'
import io from '../../api'
import Detail from './Detail'

export default {
  components: {
    Page,
    Detail
  },
  data: () => ({
    headerHeightVW: 0,
    id: null,
    detail: {}
  }),
  methods: {
    onReady (header, main) {
      this.headerHeightVW = (header.h + 1) * 100 / header.w

      this.id = navigate.getParam('id')

      this.loadDetail()
    },
    loadDetail () {
      this.$toast.loading({
        duration: 0,
        loadingType: 'spinner'
      })

      io.getTransmitDetail({id: this.id}).then(data => {
        this.detail = data
        this.$toast.clear()
      }).catch(msg => {
        this.$toast(msg)
      })
    },
    onClickLeft () {
      navigate.back()
    },

    handleReject () {
      const _this = this

      this.$dialog.confirm({
        message: '是否确认拒绝换班？',
        beforeClose (action, done) {
          if (action === 'confirm') {
            console.log(_this.id)
            io.rejectTransmit({id: _this.id}).then(() => {
              done()
              _this.loadDetail()
            }).catch(msg => {
              this.$toast(msg)
              done()
            })
          } else {
            done();
          }
        }
      })
    },

    handleAgree () {
      const _this = this

      this.$dialog.confirm({
        message: '是否确认同意换班？',
        beforeClose (action, done) {
          if (action === 'confirm') {
            console.log(_this.id)
            io.agreeTransmit({id: _this.id}).then(() => {
              done()
              _this.loadDetail()
            }).catch(msg => {
              this.$toast(msg)
              done()
            })
          } else {
            done();
          }
        }
      })
    },
  }
}
</script>

<style lang="scss">

.problem-page{
  .btn-save{
    .van-button__text{
      color: #108EE9;
    }
  }
}

</style>
